
.mail {
  padding: 50px 15% 30px 16%;
  font-family: monospace;
  color: #282828;
  font-size: 16px;
}
.mail .home {
  width: 50px;
  height: 50px;
  background: url('../../assets/home.png') no-repeat center;
  background-size: contain;
  position: absolute;
  top: 20px;
  left: 30px;
  cursor: pointer;
}
.mail .back {
  width: 50px;
  height: 50px;
  background: url('../../assets/back.png') no-repeat center;
  background-size: contain;
  position: absolute;
  top: 80px;
  left: 30px;
  cursor: pointer;
}
.mail h1 {
  font-size: 19px;
  padding: 10px 0;
  font-weight: bold;
}
.mail pre {
  background: #f7f7f7 !important;
  padding: 16px;
  overflow-x: auto;
}
.mail code {
  background: #f7f7f7;
  font-family: monospaceFontFamily !important;
}



