
.home {
  height: 400%;
  width: 100%;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.home .mail-box {
  position: absolute;
  left: 20px;
  top: 50px;
  z-index: 999;
  width: 83px;
  height: 48px;
  background: url('../../assets/mail.png') no-repeat center;
  background-size: contain;
  cursor: pointer;
}
.home .mail-box .dot {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1000;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: red;
}
.home .mail-box .dot.read {
  display: none;
}
.home .point-container {
  position: absolute;
  top: 500px;
  left: 80px;
  z-index: 999;
}
.home .point-container .point-arrow {
  width: 100px;
  height: 30px;
  background: url('../../assets/point-arrow.png') no-repeat center;
  background-size: contain;
}
.home .point-container .text {
  font-family: "PMingLiU", "LiSu", "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
  margin-left: -50px;
  margin-top: 10px;
}
.home .slot {
  position: relative;
  height: 25%;
  width: 100%;
}
.home .slot .down-arrow {
  position: absolute;
  bottom: 10px;
  width: 40.875px;
  height: 34.4375px;
  left: 50%;
  margin-left: -20.4375px;
  background: url('../../assets/down-arrow.png') no-repeat center;
  background-size: contain;
  -webkit-animation: slideInDown 1s infinite;
          animation: slideInDown 1s infinite;
}
.home .slot.main {
  position: relative;
  background-color: #33ff99;
  text-align: center;
}
.home .slot.main .text-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: fit-content;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  -webkit-animation: bounceIn 0.5s;
          animation: bounceIn 0.5s;
}
.home .slot.main .text-container .text {
  font-size: 82px;
}
.home .slot.main .text-container .date {
  font-size: 52px;
  margin-left: 300px;
}
.home .slot.memory {
  background: #00ff66;
  font-size: 16px;
  padding: 10px 250px;
  overflow: auto;
}
.home .slot.memory .title {
  padding-top: 5px;
}
.home .slot.memory .content {
  padding: 6px;
}
.home .slot.memory .content .paragraph {
  padding: 4px 0 4px 20px;
}
.home .slot.photo {
  background: #00ff33;
  position: relative;
}
.home .slot.photo .last,
.home .slot.photo .next {
  position: absolute;
  height: 100%;
  width: 10%;
  text-align: center;
  cursor: pointer;
}
.home .slot.photo .last {
  left: 0;
}
.home .slot.photo .next {
  right: 0;
}
.home .slot.photo .arrow {
  font-size: 32px;
  color: white;
  line-height: 1;
  position: relative;
  top: 50%;
  margin-top: -16px;
}
.home .slot.photo .pic-box {
