  line-height: 1;
}
@-webkit-keyframes bounceIn {
from,
  20%,
  40%,
  60%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
0% {
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale3d(0.2, 0.2, 0.2);
            transform: translate(-50%, -50%) scale3d(0.2, 0.2, 0.2);
}
20% {
    -webkit-transform: translate(-50%, -50%) scale3d(1.3, 1.3, 1.3);
            transform: translate(-50%, -50%) scale3d(1.3, 1.3, 1.3);
}
40% {
    -webkit-transform: translate(-50%, -50%) scale3d(0.9, 0.9, 0.9);
            transform: translate(-50%, -50%) scale3d(0.9, 0.9, 0.9);
}
60% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale3d(1.4, 1.4, 1.4);
            transform: translate(-50%, -50%) scale3d(1.4, 1.4, 1.4);
}
80% {
    -webkit-transform: translate(-50%, -50%) scale3d(0.97, 0.97, 0.97);
            transform: translate(-50%, -50%) scale3d(0.97, 0.97, 0.97);
}
to {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale3d(1, 1, 1);
            transform: translate(-50%, -50%) scale3d(1, 1, 1);
}
}
@keyframes bounceIn {
from,
  20%,
  40%,
  60%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
0% {
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale3d(0.2, 0.2, 0.2);
            transform: translate(-50%, -50%) scale3d(0.2, 0.2, 0.2);
}
20% {
    -webkit-transform: translate(-50%, -50%) scale3d(1.3, 1.3, 1.3);
            transform: translate(-50%, -50%) scale3d(1.3, 1.3, 1.3);
}
40% {
    -webkit-transform: translate(-50%, -50%) scale3d(0.9, 0.9, 0.9);
            transform: translate(-50%, -50%) scale3d(0.9, 0.9, 0.9);
}
60% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale3d(1.4, 1.4, 1.4);
            transform: translate(-50%, -50%) scale3d(1.4, 1.4, 1.4);
}
80% {
    -webkit-transform: translate(-50%, -50%) scale3d(0.97, 0.97, 0.97);
            transform: translate(-50%, -50%) scale3d(0.97, 0.97, 0.97);
}
to {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale3d(1, 1, 1);
            transform: translate(-50%, -50%) scale3d(1, 1, 1);
}
}
@-webkit-keyframes typing {
from {
    display: block;
    width: 0;
}
1% {
    border-right: .1em solid;
}
99% {
    border-right: .1em solid;
}
100% {
    border-right: 0;
}
}
@keyframes typing {
from {
    display: block;
    width: 0;
}
1% {
    border-right: .1em solid;
}
99% {
    border-right: .1em solid;
}
100% {
    border-right: 0;
}
}
@-webkit-keyframes blink-caret {
50% {
    border-color: transparent;
}
}
@keyframes blink-caret {
50% {
    border-color: transparent;
}
}
@-webkit-keyframes fade-in {
0% {
    opacity: 0;
}
  /*初始状态 透明度为0*/
40% {
    opacity: 0;
}
  /*过渡状态 透明度为0*/
100% {
    opacity: 1;
}
  /*结束状态 透明度为1*/
}
@keyframes fade-in {
0% {
    opacity: 0;
}
  /*初始状态 透明度为0*/
40% {
    opacity: 0;
}
  /*过渡状态 透明度为0*/
100% {
    opacity: 1;
}
  /*结束状态 透明度为1*/
}
@-webkit-keyframes slideInDown {
from {
    -webkit-transform: translate3d(0, -70%, 0);
            transform: translate3d(0, -70%, 0);
    visibility: visible;
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes slideInDown {
from {
    -webkit-transform: translate3d(0, -70%, 0);
            transform: translate3d(0, -70%, 0);
    visibility: visible;
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
.home .slideInDown {
  -webkit-animation-name: slideInDown;
          animation-name: slideInDown;
}
.home .bounceIn {
  -webkit-animation-name: bounceIn;
          animation-name: bounceIn;
}



