
.list-view .home {
  width: 50px;
  height: 50px;
  background: url('../../assets/home.png') no-repeat center;
  background-size: contain;
  position: absolute;
  top: 20px;
  left: 30px;
  cursor: pointer;
}
.list-view .item-container {
  padding: 30px 0 30px 40%;
  font-family: monospace;
  color: #282828;
}
.list-view .item-container .item-title {
  font-size: 18px;
  padding: 10px 0;
}
.list-view .item-container .date {
  display: inline-block;
  font-size: 14px;
  line-height: 18px;
  margin-left: 20px;
  color: #666;
}
.list-view .item-container .time-ago {
  margin-top: 10px;
  color: #999;
}



